import React from "react";
import {connect} from "react-redux";
import {
    followAC, followThunk, setActiveAC, setCountAC, setUserFeatchingAC, setUserLoadingAC,
    setUsersAC,
    unfollowAC, unfollowThunk, usersThunk
} from "../../redux/usersReducer";
import Users from "./Users";
import Preloader from "../common/preloader/preloader";
import { compose } from 'redux';
import withAuth from "../Hoc/WithAuth";
import {
    getIsFeatching, getPortionUsers,
    getUsersActive,
    getusersAll,
    getUsersCount,
    getUsersId, getUsersPageSelector
} from "../../redux/selectors";


class UsersContaiter extends React.Component {

    componentDidMount() {
        this.props.usersThunk(this.props.usersActive, this.props.usersCount)
    }

    onPageChange = (page) => {
        this.props.usersThunk(page,this.props.usersCount )

    }

    render() {
        return <>{
            this.props.isFeatching ? <Preloader/> : null
        }
            <Users usersAll={this.props.usersAll}
                   usersCount={this.props.usersCount}
                   onPageChange={this.onPageChange}
                   usersPage={this.props.usersPage}
                   followAC={this.props.followAC}
                   unfollowAC={this.props.unfollowAC}
                   usersActive={this.props.usersActive}
                   setUserFeatchingAC={this.props.setUserFeatchingAC}
                   setUserLoadingAC={this.props.setUserLoadingAC}
                   UsersId={this.props.UsersId}
                   followThunk={this.props.followThunk}
                   unfollowThunk={this.props.unfollowThunk}
                   setActiveAC={this.props.setActiveAC}
                   portionUsers={this.props.portionUsers}
            />
        </>
    }
}

let mapStateToProps = (state) => {
    return ({
        usersPage: getUsersPageSelector(state),
        usersCount: getUsersCount(state),
        usersActive: getUsersActive(state),
        usersAll: getusersAll(state),
        isFeatching: getIsFeatching(state),
        UsersId: getUsersId(state),
        portionUsers: getPortionUsers(state)
    })
}
