//             dispatch(setUsersActiveAC(active))
//         },
//         setUserFeatchingAC: (featching)=>{
//             dispatch(setFeatchingAC(featching))
//         }
//     }
// }

export default compose(

    connect(mapStateToProps, {
        followAC, unfollowAC, setUsersAC, setCountAC, setActiveAC, setUserFeatchingAC, setUserLoadingAC, usersThunk, followThunk, unfollowThunk
    }),
    withAuth,
)(UsersContaiter)


