import React, {useState} from "react";
import s from "./Users.module.css"
import User from "./user/User";

const Users = ({followThunk, unfollowThunk, UsersId, portionUsers, ...props}) => {

    let countPage = Math.ceil(props.usersAll / props.usersCount)
    if (countPage > 30) {
         countPage = 40
    }
    let portions =  Math.ceil(countPage / portionUsers)
    let [portionNumber , setPortionNumber] = useState(1)
    let leftPortion = (portionNumber - 1 ) * props.usersCount
    let rightPortion = portionNumber * props.usersCount

    let pages = []

    for (let i = 1; i <= countPage; i++) {
        pages.push(i)
    }

    return <div className={s.container}>
        <div className={s.paginators}>
            {portionNumber > 1 && <button  onClick={()=>(setPortionNumber(portionNumber -1))}>Prev</button>}
            {pages.filter(p => p > leftPortion && p<=rightPortion ).map(p => {
                return <span key={p.id}
                             onClick={(e) => {
                                 props.onPageChange(p)
                             }} className={props.usersActive === p && s.active}>{p}</span>
            })
            }
            {portionNumber >= 1 && portionNumber < portions && <button onClick={()=>(setPortionNumber(portionNumber + 1))}>Next</button>}
        </div>
        {
            props.usersPage.map(u => <User key={u.id} users={u} UsersId={UsersId} followThunk={followThunk} unfollowThunk={unfollowThunk} />)
        }
    </div>
}
export default Users
