import React from "react";
import s from "./user.module.css"
import userPhoto from "../../../assets/images/icons.png"
import {NavLink} from "react-router-dom";

const User = ({users, UsersId, followThunk, unfollowThunk, ...props }) => {
    return (
                <div className={s.containerTwo}>
                    <div className={s.follow}>
                        <NavLink to={'/Profile/' + users.id}><img
                            src={users.photos.small ===  null ? userPhoto: users.photos.small }/> </NavLink>

                        { users.followed ? <button disabled={UsersId.some(id=> id ===users.id)} onClick={() => {
                            followThunk(users.id)
                        }}>Unfollow</button> : <button disabled={UsersId.some(id=> id ===users.id)}  onClick={() => {
                            unfollowThunk(users.id)
                        }}>Follow</button>}
                    </div>
                    <div className={s.info}>
                        <div className={s.nameFollow}>{users.name}</div>
                        <div className={s.status}>{users.status}</div>
                        <div className={s.citeCountry}>{'users.citeCountry'}</div>
                    </div>
                </div>)}



export default User
